import React from 'react';
import {
  connect
} from 'react-redux';
import NavBar from '../../components/NavBar';
import Slide from './../../components/Slide'
import NewsList from './../../components/NewsList'
import PicWall from './../../components/PicWall'
import HotList from './../../components/HotList'
import ColumnStyle1 from './../../components/ColumnStyle1'
import * as AccountActions from './action'
import {
  bindActionCreators
} from 'redux';
import styled from 'styled-components';
const LayoutColumn = styled.div `
  width:1000px;
    margin:20px auto;
    overflow:hidden;
`;
const Extra = styled.div `
  width:640px;
  float:left;
  display:inline-block;
`;
class Home extends React.Component { // eslint-disable-line
  render() {
    return (
      <div>
      <NavBar param={this.props} />
        <LayoutColumn>
        <div className="layout-left dib">
          <Slide param={this.props}/>
        </div>
        <div className="layout-right dib">
          <div className="h2-title">最近更新</div>
          <NewsList param={this.props.newslist}/>
        </div>
        </LayoutColumn>

        <LayoutColumn>
          <Extra>
            <div className="h2-title">今日要闻</div>
            <HotList param={this.props.hotlist}/>
          </Extra>
          <div className="layout-right dib">
            <div className="h2-title">热门点击</div>
            <NewsList param={this.props.onclicklist}/>
          </div>
        </LayoutColumn>
        <LayoutColumn>
          <ColumnStyle1 param={this.props} classid='2' />
          <PicWall param={this.props}/>
        </LayoutColumn>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const stateJS = state.toJS();
  var result = {};
  // 将state中各模块的数据中取出合并
  for (var i in stateJS) {
    for (var j in stateJS[i]) {
      result[j] = stateJS[i][j]
    }
  }
  return (result);
}


function mapDispatchToProps(dispatch) {
  // 头条
  dispatch(AccountActions.GetHeadline({
    year:3,
    limit:5
  }));
  // 最近更新
  dispatch(AccountActions.GetLastList({
    length: 40
  }));
  // 今日要闻
  dispatch(AccountActions.GetNewsList({
    length: 40,
    actionType: 'hot',
    query: 'isgood',
    limit: 3
  }));
  // 热门点击排行
  dispatch(AccountActions.GetNewsList({
    length: 40,
    actionType: 'onclick',
    query: 'onclick',
    limit: 10
  }));
  // 最近更新图片墙
  dispatch(AccountActions.GetPicList({
    limit: 6,
    classid:1,
    actionType: 'picwall',
    year: 3
  }));
  return bindActionCreators({...AccountActions
  }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);


